import React from 'react'
import Link from 'gatsby-link'
import styled, { keyframes } from 'styled-components'
import Empty from '../assets/cursor/empty.svg'
import Filled from '../assets/cursor/filled.svg'
import {start, Start, rainbow, StyledText, Content, ContentGroup, Parent, Sub, Body, BodyImage, StyledLink, Glow} from './styles.js'

class I2WE extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      hover1: false,
      hover2: false,
      hover3: false,
      hover4: false
    }
  }

  mouseOver = (p) => {
    this.setState({[p]: true})
    console.log(this.state.hover)
  }

  mouseOut = (p) => {
    this.setState({[p]: false})
    console.log(this.state.hover)
  }

  render() {
    return (
      <div>
        <Start>
          <Content>
            <ContentGroup>
              <p>
                <Glow>
                I2we
                </Glow>

                <Sub>
                Aug 2020 - Nov 2020
                </Sub>

                <Body>
                I2WE is an iOS dating app I worked on with a team of three others for our Database Systems final project.
                We used Swift for the front end and Firebase as the backend to implement user registration and login, a profile page, a search page where users can search for other users based on certain criteria (i.e. age, school), and a fully-featured chat system (text, images, location, audio, video).
                </Body>
              </p>
            </ContentGroup>
          </Content>
        </Start>
      </div>
    )
  }
}

export default I2WE



